/**
 * breakpoint-up/down - Change color of a css property based on the currently active theme
 *
 * @param  {size}     min/max size of the screen
 *
 * @example:-
 *
 * HIDE FOR DESKTOP:
 @include breakpoint-down(small) {
    background: red;
 }
 * HIDE FOR MOBILE:
 @include breakpoint-up(medium) {
    background: blue;
 }
 *
 * if you are designing your layout with a mobile-first approach, then use min-width breakpoints and work your way up.
 * if you are designing for larger devices first, then set your default CSS as you normally would and adjust for smaller devices with the max-width approach.
*/
.header {
  margin: 0.5rem;
}
.header .overlay {
  display: none;
}
@media (max-width: 63.9375em) {
  .header .overlay {
    position: absolute;
    width: 100%;
    left: 100%;
    transform: translateX(0%);
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-y: auto;
    transition: 0.5s;
  }
  .header .overlay.act {
    transform: translateX(-100%);
  }
  .header .overlay ul {
    position: relative;
  }
  .header .overlay ul li {
    position: relative;
    list-style: none;
    text-align: center;
    display: block;
    height: auto;
  }
  .header .overlay ul li a {
    position: relative;
    color: blue;
    text-decoration: none;
    font-size: 1.5em;
    padding: 0 1rem;
    font-weight: 700;
    text-transform: uppercase;
  }
  .header .overlay ul .collection--ul {
    display: none;
  }
  .header .overlay ul .collection--ul li {
    text-align: center;
  }
  .header .overlay ul .collection--ul li a {
    color: greenyellow;
    text-decoration: none;
  }
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.header .__links ul {
  display: flex;
  justify-content: center;
  list-style: none;
}
.header__icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__cart {
  height: 1rem;
  width: 2.5rem;
  margin-bottom: 1.5rem;
  margin-right: 0.5rem;
  color: black;
}
.header__ham {
  display: none;
}
@media (max-width: 63.9375em) {
  .header__ham {
    display: flex;
    flex-direction: column;
    gap: 0.4rem;
    cursor: pointer;
    margin-right: 1rem;
  }
  .header__ham__line--1, .header__ham__line--2, .header__ham__line--3 {
    height: 0.3rem;
    width: 2rem;
    background: #000;
    border-radius: 8px;
  }
  .header__ham__line--1 {
    animation: top-back 0.4s ease-in-out forwards;
  }
  .header__ham__line--2 {
    transform: scale(1);
    transition: 0.4s ease-in-out;
  }
  .header__ham__line--3 {
    animation: bot-back 0.4s ease-in-out forwards;
  }
  .header__ham.act .header__ham__line--1 {
    animation: top-forw 0.4s ease-in-out forwards;
  }
  .header__ham.act .header__ham__line--2 {
    transform: scale(0);
    transition: 0.4s ease-in-out;
  }
  .header__ham.act .header__ham__line--3 {
    animation: bot-forw 0.4s ease-in-out forwards;
  }
}

@keyframes top-forw {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(0.7rem);
  }
  100% {
    transform: translateY(0.7rem) rotate(45deg);
  }
}
@keyframes bot-forw {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-0.7rem);
  }
  100% {
    transform: translateY(-0.7rem) rotate(135deg);
  }
}
@keyframes top-back {
  0% {
    transform: translateY(0.7rem) rotate(45deg);
  }
  50% {
    transform: translateY(0.7rem);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes bot-back {
  0% {
    transform: translateY(-0.7rem) rotate(135deg);
  }
  50% {
    transform: translateY(-0.7rem);
  }
  100% {
    transform: translateY(0);
  }
}